import axios from 'axios'
import { Notification, MessageBox, Message, Loading } from 'element-ui'
import { getToken, removeToken } from '@/utils/auth'
import errorCode from '@/utils/errorCode'
import { tansParams, blobValidate } from '@/utils/ruoyi'
import cache from '@/utils/cache'
import { saveAs } from 'file-saver'
import { logout } from '@/api/login'

let downloadLoadingInstance
// 
export const isRelogin = { show: false }

axios.defaults.headers['Content-Type'] = 'application/json;charset=utf-8'
// axios
const service = axios.create({
  // axiosbaseURLURL
  baseURL: '/api',
  // 
  timeout: 10000
})

// request
service.interceptors.request.use(config => {
  //  token
  const isToken = (config.headers || {}).isToken === false
  // 
  const isRepeatSubmit = (config.headers || {}).repeatSubmit === false
  if (getToken() && !isToken) {
    config.headers.Authorization = 'Bearer ' + getToken() // token 
  }
  // getparams
  if (config.method === 'get' && config.params) {
    let url = config.url + '?' + tansParams(config.params)
    url = url.slice(0, -1)
    config.params = {}
    config.url = url
  }
  if (!isRepeatSubmit && (config.method === 'post' || config.method === 'put')) {
    const requestObj = {
      url: config.url,
      data: typeof config.data === 'object' ? JSON.stringify(config.data) : config.data,
      time: new Date().getTime()
    }
    const sessionObj = cache.session.getJSON('sessionObj')
    if (sessionObj === undefined || sessionObj === null || sessionObj === '') {
      cache.session.setJSON('sessionObj', requestObj)
    } else {
      const surl = sessionObj.url // 
      const sdata = sessionObj.data // 
      const stime = sessionObj.time // 
      const interval = 1000 // (ms)
      if (sdata === requestObj.data && requestObj.time - stime < interval && surl === requestObj.url) {
        const message = ''
        console.warn(`[${surl}]: ` + message)
        return Promise.reject(new Error(message))
      } else {
        cache.session.setJSON('sessionObj', requestObj)
      }
    }
  }
  return config
}, error => {
  console.log(error)
  Promise.reject(error)
})

// 
service.interceptors.response.use(res => {
  // 
  const code = res.data.code || 200
  // 
  const msg = errorCode[code] || res.data.msg || errorCode.default
  // 
  if (res.request.responseType === 'blob' || res.request.responseType === 'arraybuffer') {
    return res.data
  }
  if (code === 401) {
    if (!isRelogin.show) {
      isRelogin.show = true
      MessageBox.confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }
      ).then(() => {
        isRelogin.show = false
        logout().then(res => {
          removeToken()
          location.href = '/login'
        }).catch(() => {})
      }).catch(() => {
        isRelogin.show = false
      })
    }
    return Promise.reject(new Error(''))
  } else if (code === 500) {
    Message({
      message: msg,
      type: 'error'
    })
    return Promise.reject(new Error(msg))
  } else if (code !== 200) {
    Notification.error({
      title: msg
    })
    return Promise.reject(new Error('error'))
  } else {
    return res.data
  }
},
error => {
  console.log('err' + error)
  let { message } = error
  if (message === 'Network Error') {
    message = ''
  } else if (message.includes('timeout')) {
    message = ''
  } else if (message.includes('Request failed with status code')) {
    message = '' + message.substr(message.length - 3) + ''
  }
  Message({
    message: message,
    type: 'error',
    duration: 5 * 1000
  })
  return Promise.reject(error)
}
)

// 
export function download (url, params, filename) {
  downloadLoadingInstance = Loading.service({ text: '', spinner: 'el-icon-loading', background: 'rgba(0, 0, 0, 0.7)' })
  return service.post(url, params, {
    transformRequest: [(params) => { return tansParams(params) }],
    headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
    responseType: 'blob'
  }).then(async (data) => {
    const isLogin = await blobValidate(data)
    if (isLogin) {
      const blob = new Blob([data])
      saveAs(blob, filename)
    } else {
      const resText = await data.text()
      const rspObj = JSON.parse(resText)
      const errMsg = errorCode[rspObj.code] || rspObj.msg || errorCode.default
      Message.error(errMsg)
    }
    downloadLoadingInstance.close()
  }).catch((r) => {
    console.error(r)
    Message.error('')
    downloadLoadingInstance.close()
  })
}

export default service
